.todos__single {
	display: flex;
	width: 29.5%;
	border-radius: 5px;
	padding: 20px;
	margin-top: 15px;
	background-color: bisque;
}

.todos__single--text {
	flex: 1;
	padding: 5px;
	border: none;
	font-size: 20px;
}

.todos__single--text:focus {
	outline: none;
}

.icon {
	margin-left: 10px;
	font-size: 25px;
	cursor: pointer;
}
